@tailwind base;
@tailwind components;
@tailwind utilities;


.box{
    @apply bg-gradient-to-t from-gray-300 rounded-b-xl p-3
}

.btn{
    transition: transform 0.3s ease, background-color 0.3s ease, color 0.3s ease;
}
.btn:hover {
    transform: scale(1.2);
    color: rgb(30,64,175);
}

:where(.css-dev-only-do-not-override-gzal6t).ant-menu .ant-menu-item a::before {
 content: none;
}

:where(.css-dev-only-do-not-override-gzal6t).ant-menu-inline-collapsed >.ant-menu-item .ant-menu-item-icon{
    font-size: 35px;
}

:where(.css-dev-only-do-not-override-gzal6t).ant-menu-inline-collapsed >.ant-menu-item{
    padding-inline: 20px;
}

.ant-menu-inline-collapsed-tooltip {
    max-width: unset;

    .ant-tooltip-inner {
        word-wrap: unset;
    }
}